html {
  height: 100%; }

body {
  font-family: 'Open Sans';
  color: white;
  min-height: 100%;
  background: -webkit-linear-gradient(bottom, #2b1339, #380c77);
  background: linear-gradient(bottom, #2b1339, #380c77); }

body.landing {
  background: #5b5579; }

body.landing .navbar {
  margin-bottom: 0; }

.player-header-nav {
  background-color: rgba(123, 70, 163, 0.9);
  /* medium-purple: #7B46A3 - 123, 70, 163 */ }

.hero-content {
  background: #2f1d57;
  padding: 100px 0; }

.hero-content h1 {
  font-size: 70px;
  margin-bottom: 50px;
  font-weight: 300; }

.selling-points {
  padding: 100px 0; }

.selling-points .point {
  width: 30%;
  float: left;
  padding: 0 1.5%;
  text-align: center; }

.selling-points .point h5 {
  font-size: 24px;
  font-weight: bold;
  margin-bottom: 20px;
  text-align: center;
  height: 85px; }

.selling-points .point p {
  font-size: 14px; }
